<template>
  <div class="comment-list" v-if="commentLst.length">
    <div class="list" v-for="(comment,index) in commentLst" :key="index" @click="replyComment(comment,index)">
      <div class="list-item">
        <img @error="defaultImg($event, comment.commentUser.sex)" :src="account.serverHost + comment.commentUser.portraitURL" class="item-avatar">
        <div class="item-detail">
          <span class="detail-name">
            <span class="commentUser">{{comment.commentUser.name}}</span>
            <span class="reply" v-if="detailName(comment)">{{$t('reply')}}</span>
            <span class="replyUser" v-if="detailName(comment)">{{comment.replyUser.name}}</span>
            <span>:</span>
          </span>
          <!-- <span class="detail-name">{{comment.commentUser.name}}</span> -->
          <span class="detail-content" v-html="index == fullAllIndex ? comment.content.replace(/\n/g, '<br/>') : comment.content.substr(0, 100).replace(/\n/g, '<br/>')"></span>
          <span class="open-close" v-if="comment.content.length > 100" @click="fullAllIndex == index?fullAllIndex = -1:fullAllIndex = index">{{fullAllIndex == index?$t('collapse'):$t('expandAll')}}</span>
          <div class="detail-time">
            <span>{{formatCreateTime(comment.createTime)}}</span>
            <!--account.mUserIdaccount.id  -->
            <span class="delete-comment"
              v-if="comment.commentUserID == account.id"
              @click="deleteComment(index, comment,$event)"
            >{{$t('delete')}}</span>
            <!-- <span
              class="reply-comment"
              v-if="comment.commentUserID != account.id"
              @click="replyComment(index)"
            >{{$t('reply')}}</span> -->
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import * as DateUtils from '../../../utils';
import { getDefaultIcon } from './../../common/util.js';
import commentData from '../../../assets/data/comment.js';
export default {
  data () {
    return {
      defaultAvatar: require('../../../assets/images/common/dou.png'),
      fullAllIndex: -1
    };
  },
  props: {
    commentLst: Array,
    account: Object,
    index: Number
  },
  computed: {
    detailName() {
      return function(comment) {
        return this.replyCommentName(comment);
      }
    }
  },
  methods: {
    replyCommentName(comment) {
      if(comment.replyUserID) {
        return true;
      }else {
        return false;
      }
    },
    defaultImg (e, sex) {
      e.target.src = getDefaultIcon(sex);
    },
    formatCreateTime (time) {
      return DateUtils.format(time);
    },
    replyComment (c, i) {
      console.log('', c);
    },
    deleteComment (index, comment,ev) {
      console.log(index, comment);
      ev.stopPropagation();
      this.$alert.confirm({
        confirmText: `${this.$t('confirmDelete')}?`,
        title: `${this.$t('notification')}!`,
        onConfirm: () => {
          let commentBean = {
            commentID: comment.commentID,
            subjectID: comment.subjectID
          };
          let deleteCommentParam = {
            commentBean
            // 'success': resp => {
            //   console.log(resp, '');
            //   // this.$delete(this.commentLst, index);
            //   this.$store.commit('SHARE_LIST', {flag: 7, shareIndex: this.index, commentIndex: index});
    
            // }
          };
          // vrv.jssdk.deleteComment(deleteCommentParam);
          this.$agent.deleteComment(deleteCommentParam,resp => {
            console.log(resp, '');
            // this.$delete(this.commentLst, index);
            this.$store.commit('SHARE_LIST', {flag: 7, shareIndex: this.index, commentIndex: index});
          })
        }
      })
    }
  }
};
</script>

<style lang="scss" scoped>
.comment-list {
  //
  background-color: rgb(218, 214, 218);
  padding: 5px 10px;
  border-bottom-left-radius: 5px;
  border-bottom-right-radius: 5px;
  .list {
    flex: 1;
    border-radius: 2px;
    font-size: 14px;
    .list-item {
      display: flex;
      align-items: flex-start;
      padding: 6.5px 0px 6.5px 0px;
      .item-avatar {
        width: 36px;
        height: 36px;
        border-radius: 50%;
        margin-right: 8px;
        background: #A4A8AC;
        border: 1px solid rgba(0, 0, 0, .05);
      }
      .item-detail {
        width: 90%;
        word-wrap:break-word;
        //
        font-size: 12px;
        .detail-name {
          color: #3c91e9;
          .reply{
            padding: 0px 5px;
            color: rgb(50, 50, 50);
          }
        }
        .detail-content{
          color: rgb(50, 50, 50);
        }
        .detail-time {
          color: #A4A8AC;
          -webkit-user-select: none;
          //
          font-size: 10px;
          -webkit-text-size-adjust: 10px;
          .delete-comment{
            padding-left: 5px;
            cursor: pointer;
            color: #3c91e9;
          }
          .reply-comment{
            padding-left: 5px;
            cursor: pointer;
            color: #3c91e9;
          }
        }
        .open-close{
          // color: rgba(255, 102, 0, 0.952);
          color: #3c91e9;
          // font-size: 14px;
          //
          font-size: 12px;
          cursor: pointer;
          display: inline-block
        }
      }
    }
  }
}
</style>



