<template>
  <div class="cropper">
    <vue-cropper
      ref="cropper"
      :img="imgInfo.img"
      :outputSize="imgInfo.size"
      :outputType="imgInfo.outputType"
      :info="imgInfo.info"
      :canScale="imgInfo.canScale"
      :autoCrop="imgInfo.autoCrop"
      :autoCropWidth="imgInfo.width"
      :autoCropHeight="imgInfo.height"
      :fixed="imgInfo.fixed"
      :fixedNumber="imgInfo.fixedNumber"
      @changeCropEnd="finish()"
    ></vue-cropper>
    <label class="btn" for="upload" v-if="imgInfo.img==''">{{$t('selectImage')}}</label>
    <input
      class="select-btn"
      type="file" id="upload"
      style="position:absolute; clip:rect(0 0 0 0);"
      accept="image/png, image/jpeg, image/gif, image/jpg"
      @change="uploadImg($event)"
    >
  </div>
</template>

<script>
import vueCropper from './vue-cropper';
import { mapGetters } from 'vuex';
export default {
  data: function() {
    return {
      crap: false,
      previews: {},
      option: {
        img: '',
        size: 1,
        outputType: 'jpeg'
      },
      imgInfo: {
        img: '',
        info: true,
        size: 1,
        outputType: 'jpeg',
        canScale: true,
        autoCrop: true,
        //  
        width: 500,
        height: 125,
        fixed: true,
        fixedNumber: [4, 1]
      }
    };
  },
  props: {
    // ...
  },
  computed: {
    ...mapGetters(['user_account'])
  },
  watch: {
    // ...
  },
  created() {
    // ...
  },
  methods: {
    finish() {
      this.$refs.cropper.getCropData(data => {
        this.option.img = data;
        this.$emit('uploading', data);
        // if (this.$props.saveImg) {
        //   
        //   this.$emit('uploading', data);
        // }
      });
    },
    uploadImg(e) {
      var file = e.target.files[0];
      if (!/\.(gif|jpg|jpeg|png|bmp|GIF|JPG|PNG)$/.test(e.target.value)) {
        alert(this.$t('imageFormatError'));
        return false;
      }
      var reader = new FileReader();
      reader.onload = e => {
        this.imgInfo.img = e.target.result;
      };
      reader.readAsDataURL(file);
    }
  },
  components: {
    vueCropper
  }
};
</script>

<style lang="scss" scoped>
* {
  margin: 0;
  padding: 0;
}

.content {
  margin: auto;
  height: 308px;
  max-width: 660px;
  margin-bottom: 100px;
}

.btn {
  position: absolute;
  height: 40px;
  // width: 95px;
  top: 50%;
  left: 50%;
  margin-top: -20px;
  margin-left: -50px;
  background: #a9a9a9;
  color: #1f2d3d;
  padding: 9px 15px;
  font-size: 16px;
  border-radius: 5px;
  color: #fff;
}

.select-btn{
  width: 10px;
  height: 10px;
  position: absolute;
  top: 50%;
  left: 50%;
  margin-top: -5px;
  margin-left: -5px;
}

.show-info {
  overflow: hidden;
  width: 100%;
  .is-mobile {
    height: 308px;
    margin: 0px;
    .is-9 {
      background: #f0f0f0;
      padding: 0px;
      color: #ffffff;
      text-align: center;
    }
    .is-3 {
      background: #ffffff;
      padding: 0px;
      display: flex;
      justify-content: center;
      .show-preview{
        .Thumbnail100{
          width: 100px;
          height:100px;
          margin: 0 auto;
          margin-bottom:20px;
        }
        .Thumbnail48{
          width: 48px;
          height:48px;
          margin: 0 auto;
          margin-bottom:20px;
        }
        .Thumbnail24{
          width: 24px;
          height:24px;
          margin: 0 auto;
          margin-bottom:20px;
        }
      }
    }
  }
}

.cropper {
  height: 308px;
  background: #f0f0f0;
  position: relative;
}

@keyframes slide {
  0% {
    background-position: 0 0;
  }
  100% {
    background-position: -100% 0;
  }
}
</style>



