import Vue from 'vue';

const note = {
  state: {
    account: {},
    shareList: []
  },
  mutations: {
    SET_ACCOUNT: (state, data) => {
      switch (data.flag) {
        case 0: {
          state.account = data.val;
          break;
        }
        case 1: {
          Vue.set(state.account, 'backgroundUrl', data.val);
          break;
        }
      }
    },
    SHARE_LIST: (state, data) => {
      console.log(data);
      switch (data.flag) {
        case 0: {
          data.list.forEach(element => {
            let temp = {};
            for (let e in element) {
              Vue.set(temp, e, element[e]);
            }
            Vue.set(state.shareList, state.shareList.length, temp);
          });
          break;
        }
        case 1: { // 
          data.list.forEach(element => {
            let temp = {};
            for (let e in element) {
              Vue.set(temp, e, element[e]);
            };
            console.log(temp, '');
            Vue.set(state.shareList, state.shareList.length, temp);
          });
          break;
        }
        case 2: { // 
          console.log('vuex data.list:::', data.list);
          let addstr = JSON.stringify(data.list);
          state.shareList.unshift(JSON.parse(addstr));
          break;
        }
        case 3: { // 
          Vue.delete(state.shareList, data.index);
          break;
        }
        case 4: { // 
          console.log(data, '');
          Vue.set(
            state.shareList[data.index].praiseList,
            state.shareList[data.index].praiseList.length,
            data.praiseShowInfo
          );
          console.log(state.shareList, '');
          break;
        }
        case 5: { // 
          console.log(data, 'vuex ');
          let praiseList = state.shareList[data.index].praiseList;
          let shareObj = Object.assign({}, state.shareList[data.index]);
          for (let i = 0; i < praiseList.length; i++) {
            //  account.mUserIdaccount.id
            if (praiseList[i].userID.toString() === state.account.id) {
              console.log(praiseList[i].userID.toString() === state.account.id, 'id');
              Vue.delete(praiseList, i);
              break;
            }
          };
          break;
        }
        case 6: {
          console.log(data, '');
          let commentLst = state.shareList[data.index].commentPage.commentLst;
          let commentPage = state.shareList[data.index].commentPage;
          let commentObj = Object.assign({}, state.shareList[data.index]);
          Vue.set(commentLst, commentLst.length, data.comment);
          Vue.set(commentPage, 'commentLst', commentLst);
          Vue.set(state.shareList[data.index], 'commentPage', commentPage);
          Vue.set(state.shareList, data.index, commentObj);
          // Vue.set(commentLst, commentLst.length, data.comment);
          break;
        }
        case 7: {
          console.log(data, '');
          let commentLst = state.shareList[data.shareIndex].commentPage.commentLst;
          let commentPage = state.shareList[data.shareIndex].commentPage;
          let commentObj = Object.assign({}, state.shareList[data.shareIndex]);
          Vue.delete(commentLst, data.commentIndex);
          Vue.set(commentPage, 'commentLst', commentLst);
          Vue.set(state.shareList[data.shareIndex], 'commentPage', commentPage);
          Vue.set(state.shareList, data.shareIndex, commentObj);
          // Vue.set(commentLst, commentLst.length, data.comment);
        }
      }
    }
  }
};

export default note;



