<template>
	<div class="colleague-out-box">
    <div
      class="colleague-poster"
      :style="{height: isOperatArea?'auto':'95px'}"
    >
      <div class="editor-area">
        <div
          class="editor-box placeholder-tip"
          contenteditable="true"
          :data-placeholder="$t('shareSomething')"
          wrap="hard"
          ref="getMsg"
          @focus="isOperatArea = true"
          @input="getScrollHeight"
        >
        </div>
        <span class="remaining-Word">{{remainingWord}}/1000</span>
        <div>
          <div></div>
          <div></div>
        </div>
      </div>
      <div class="operat-area">
        <div class="left">
          <label class="upload-img">
            <i
              class="iconfont icon-tupian1"
              @click="addImg"
              :style="{
                pointerEvents: isAddImg?'auto':'none',
                color: isAddImg?'':'#c7c7cc'
              }"
            ></i>
          </label>
        </div>
        <div class="right">
          <span class="showType-span" v-if="isShowTypeSelectArea">
            {{$t('visibleRange')}}
            <span class="choose-item" @click="isShowType = true">
              {{whoToSee}}
              <i class="iconfont icon-xia"></i>
            </span>
          </span>
          <button
            @click="postedShare"
            :style="{
              background: (isPosted || imgPathsSize > 0)?'#1995e4':'#94d1f7',
              pointerEvents: (isPosted || imgPathsSize > 0)?'auto':'none'
            }"
          >{{$t('publish')}}</button>
        </div>
      </div>
    </div>
    <div class="picture-preview" v-if="imgPathsSize > 0">
      <div class="remaining-img">{{$t('img')}}:({{imgPathsSize}}/9)</div>
      <div class="preview-box">
        <div
          class="small-img-box"
          v-for="item in imgLocalPaths"
          :key="item.imgId"
        >
          <img :src="item.img">
          <div v-if="item.pro!==100" class="upload-pro">{{item.pro}}%</div>
          <i class="iconfont icon-shanchu-01" @click="delImg(item.imgId)"></i>
        </div>
      </div>
    </div>
		<div class="showType-list-box" v-if="isShowType">
      <div
        class="showType-item"
        v-for="(item, index) in showTypeList"
        :key="index"
        @click="moreAction(item)"
      >{{item.title}}</div>
    </div>
    <!--  -->
    <w-modal :cardSize="cardSize" :isShowModal="isShowModal" @close="isShowModal = false">
      <power-modal
        @close="isShowModal = false"
        @set-group-type="setGroupType"
        :tempGroupMember="tempGroupMember"
      ></power-modal>
    </w-modal>
	</div>
</template>
<script>
import { mapGetters } from 'vuex';
// import temp111 from './temp111.js'
import powerModal from './../powerModal/index.vue';
import Mixin from './../common/mixin.js'
export default {
  mixins: [Mixin],
  components: {
    powerModal
  },
  data () {
    return {
      isOperatArea: false,
      messagContent: '',
      isPosted: false,
      remainingWord: 1000,
      imgLocalPaths: {}, // 
      websiteAdds: {}, //  
      deletedForWait: [],
      isAddImg: true, // 
      isShowType: false, // 
      whoToSee: this.$t('public'),
      scope: 'public',
      showTypeList: [
        {title: this.$t('public'), type: 'public'},
        {title: this.$t('private'), type: 'private'},
        {title: this.$t('partFriends'), type: '1'},
        {title: this.$t('privateTo'), type: '2'},
      ],
      cardSize: {
        width: '500px',
        height: '440px'
      },
      isShowModal: false,
      tempGroupMember: {},
      isShowTypeSelectArea: false
    };
  },
  computed: {
    ...mapGetters(['share_list', 'user_account']),
    // 
    imgPathsSize() {
      return Object.keys(this.imgLocalPaths).length;
    }
  },
  mounted () {
    document.addEventListener('click', e => {
      e.stopPropagation();
      if (this.isOperatArea && this.messagContent === '' && this.imgPathsSize === 0) {
        let colleaguePosterBox = document.getElementsByClassName('colleague-out-box')[0];
        if (!colleaguePosterBox.contains(e.target)) {
          this.isOperatArea = false;
          this.isShowType = false;
        };
      }
      if (this.isShowType) {
        let showTypeListBox = document.getElementsByClassName('showType-list-box')[0];
        if (!showTypeListBox.contains(e.target)) {
          this.isShowType = false;
        };
      }
    });
  },
  watch: {
    // 
    messagContent: function (val) {
      let msgBox = this.$refs.getMsg;
      this.messagContent === '' ? msgBox.classList.add('placeholder-tip') : msgBox.classList.length !== 1 ? msgBox.classList.remove('placeholder-tip') : '';
      val.trim() ? this.isPosted = true : this.isPosted = false;
      this.remainingWord = 1000 - this.messagContent.split('').length;
      if (this.remainingWord < 0) this.remainingWord = 0;
    }
  },
  methods: {
    // 
    setGroupType(e) {
      this.tempGroupMember = e.groupMembers;
    },
    getScrollHeight() {
      this.messagContent = this.$refs.getMsg.innerText;
    },
    // 
    moreAction (item) {
      console.log(item);
      this.whoToSee = item.title;
      this.scope = item.type;
      this.isShowType = false;
      if (item.type == '1' || item.type == '2') this.isShowModal = true;
    },
    // 
    addImg () {
      if (this.imgPathsSize > 8) {
        alert(this.$t('uploadLimit'));
        return;
      };
      let imgSize = 9 - this.imgPathsSize;
      let getImgUrlParam = {
        size: 9,
        currentSize: this.imgPathsSize,
        // 
        'select': resp => {
          console.log(':::', resp);
          resp.forEach(element => {
            this.$set(element, 'pro', 0);
            this.$set(this.imgLocalPaths, element.imgId, element);
          });
        }
        // 'success': resp => {
        //   console.log('::::::', resp);
        //   if (resp.resultCode === 0) {
        //     this.isPosted = true;
        //     if (this.deletedForWait.includes(resp.imgId)) return;
        //     this.$set(this.websiteAdds, resp.imgId, resp.mPhotoPath.url)
        //   }
        // },
        // 'progress': pro => {
        //   console.log(``, pro);
        //   this.$set(this.imgLocalPaths[pro.imgId], 'pro', pro.progess)
        // }
      };
      // vrv.jssdk.uploadShareImage(getImgUrlParam);
      this.$agent.uploadShareImage(getImgUrlParam,resp => {
          console.log('::::::', resp);
          if (resp.resultCode === 0) {
            this.isPosted = true;
            if (this.deletedForWait.includes(resp.imgId)) return;
            this.$set(this.websiteAdds, resp.imgId, resp.mPhotoPath.url);
          }
        },pro => {
           console.log(``, pro);
          this.$set(this.imgLocalPaths[pro.imgId], 'pro', pro.progess)
        }
      )
    },

    // 
    delImg(imgId) {
      console.log(imgId);
      this.deletedForWait.push(imgId);
      this.$delete(this.imgLocalPaths, imgId);
      this.$delete(this.websiteAdds, imgId);
      //false
      if(!this.imgPathsSize > 0 && this.messagContent === '') {
        this.isPosted = false;
      }
      console.log('this.imgLocalPaths:::', this.imgLocalPaths);
      console.log('this.websiteAdds:::', this.websiteAdds);
    },
    // 
    postedShare () {
      let imgLoading = Object.values(this.imgLocalPaths).every((e) => {
        return (e.pro === 100)
      })
      if (!imgLoading) {
        this.$toast.tip({message: this.$t('waitUpload')});
        return;
      }
      // 
      if (this.lengthValidator(this.messagContent, 1000)) return;
      // 
      let presentShareData = {
        praiseList: [],
        share: {
          shareID: '',
          shareUser: {
            name: this.user_account.name,
            portraitURL: this.user_account.avatarUrl,
            sex: this.user_account.gender
          }
        },
        commentPage: {
          commentLst: []
        }
      };
      let share = {
        //user_account.mUserIduser_account.id
        userID: this.user_account.id, // ID
        content: this.messagContent, // 
        longitude: 0, // 
        latitude: 0, // 
        time: new Date().getTime(), // 
        address: '', // 
        scope: this.scope, // public enable private
        type: 1, // 12
        showType: 2, // 123
        /*         */
        detailContent: {
          content: '',
          imgurls: Object.values(this.websiteAdds),
          transferUserID: '',
          resource: {},
          imgurlsIterator: {},
          imgurlsSize: 9
        },
        relatedUserIds: [] // id
      };
      let postedShareParam = {
        share
        // 'success': (resp) => {
        //   if (resp.resultCode === 0) {
        //     let tempAdds = [];
        //     Object.values(this.imgLocalPaths).filter((e) => {tempAdds.push('file::::'+e.img)});
        //     share.detailContent.imgurls = tempAdds;
        //     presentShareData.share.shareID = resp.resultMessage;
        //     Object.assign(presentShareData.share, share);
        //     this.$store.commit('SHARE_LIST', {flag: 2, list: presentShareData});
        //     this.messagContent = '';
        //     this.$refs.getMsg.innerHTML = '';
        //     this.imgLocalPaths = {};
        //     this.websiteAdds = {};
        //     this.isOperatArea = false;
        //     this.isPosted = false;
        //     this.tempGroupMember = {};
        //     this.whoToSee = this.$t('public');
        //     this.scope = 'public'
        //   }
        // }
      };
      if (this.scope == '1' || this.scope == '2') {
        let userGroup = {
          groupMembers: Object.keys(this.tempGroupMember),
          type: this.scope
        };
        Object.assign(postedShareParam, {userGroup: userGroup})
        console.log(postedShareParam, '--------1 || 2--------');
        // vrv.jssdk.addShareByTemporaryGroup(postedShareParam);
        this.$agent.addShareByTemporaryGroup(postedShareParam,resp => {
          if (resp.resultCode === 0) {
            let tempAdds = [];
            Object.values(this.imgLocalPaths).filter((e) => {tempAdds.push('file::::'+e.img)});
            share.detailContent.imgurls = tempAdds;
            presentShareData.share.shareID = resp.resultMessage;
            Object.assign(presentShareData.share, share);
            this.$store.commit('SHARE_LIST', {flag: 2, list: presentShareData});
            this.messagContent = '';
            this.$refs.getMsg.innerHTML = '';
            this.imgLocalPaths = {};
            this.websiteAdds = {};
            this.isOperatArea = false;
            this.isPosted = false;
            this.tempGroupMember = {};
            this.whoToSee = this.$t('public');
            this.scope = 'public'
          }
        })
      } else {
        // vrv.jssdk.addShare(postedShareParam);
        this.$agent.addShare(postedShareParam,resp => {
          if (resp.resultCode === 0) {
            let tempAdds = [];
            Object.values(this.imgLocalPaths).filter((e) => {tempAdds.push('file::::'+e.img)});
            share.detailContent.imgurls = tempAdds;
            presentShareData.share.shareID = resp.resultMessage;
            Object.assign(presentShareData.share, share);
            this.$store.commit('SHARE_LIST', {flag: 2, list: presentShareData});
            this.messagContent = '';
            this.$refs.getMsg.innerHTML = '';
            this.imgLocalPaths = {};
            this.websiteAdds = {};
            this.isOperatArea = false;
            this.isPosted = false;
            this.tempGroupMember = {};
            this.whoToSee = this.$t('public');
            this.scope = 'public'
          }
        })
      }
    }
  }
}
</script>
<style lang="scss" scoped>
.colleague-out-box{
  position: relative;
  margin-bottom: 10px;
  // margin-top: 225px;
  .showType-list-box{
    position: absolute;
    top: 135px;
    right: 105px;
    width: auto;
    height: auto;
    border: 1px solid #a0a0a0;
    border-radius: 4px;
    padding: 3px 0;
    z-index: 999;
    background: #fff;
    box-shadow: 0px 3px 5px -1px rgba(0, 0, 0, 0.2),
                0px 6px 10px 0px rgba(0, 0, 0, 0.14),
                0px 1px 18px 0px rgba(0, 0, 0, 0.12);
    .showType-item{
      padding: 3px 5px;
      text-align: center;
      color: rgba(0, 0, 0, 0.7);
      //
      font-size: 12px;
      &:hover{
        background: #A4A8AC;
        cursor: pointer;
        color: #fff;
      }
    }
    &::after{
      position: absolute;
      top: -5px;
      right: 18px;
      content: '';
      border-left: 7px solid transparent;
      border-right: 7px solid transparent;
      border-bottom: 6px solid #fff;
    }
    &::before{
      position: absolute;
      top: -6px;
      right: 18px;
      content: '';
      border-left: 7px solid transparent;
      border-right: 7px solid transparent;
      border-bottom: 6px solid #a0a0a0;
    }
  }
}
.colleague-poster {
  background: #fff;
  width: 100%;
  height: auto;
  overflow: hidden;
  padding: 10px;
  transition: height 0.2s;
  .editor-area {
    width: 100%;
    height: auto;
    display: flex;
    justify-content: center;
    position: relative;
    .remaining-Word{
      //
      font-size: 12px;
      color: #b9b9b9;
      position: absolute;
      right: 10px;
      bottom: 3px;
    }
    .editor-box {
      width: 100%;
      min-height: 70px;
      max-height: 115px;
      padding: 3px;
      outline: none;
      font-size: 14px;
      word-wrap: break-word;
      overflow-x: hidden;
      overflow-y: auto;
      border-color: rgba(82, 168, 236, 0.8);
      -webkit-user-modify:read-write-plaintext-only;
      margin-bottom: 15px;
    }
    .placeholder-tip{
      &::after  {
        content: attr(data-placeholder);
        color: #a9a9a9;
      }
    }
  }
  .operat-area {
    display: flex;
    justify-content: space-between;
    padding-top: 10px;
    border-top: 1px solid rgb(128, 126, 126);
    .left {
      width: 100px;
      height: 100%;
      .icon-tupian1 {
        font-size: 16px;
        padding-top: 5px;
        color: rgb(117, 117, 117);
        &:hover {
          color: rgb(36, 36, 36);
        }
      }
    }
    .right {
      width: auto;
      height: auto;
      position: relative;
      .showType-span{
        color: #757575;
        // font-size: 13px;
        font-size: 12px;
        position: relative;
        padding-right: 30px;
        //
        .choose-item{
          &:hover{
            cursor: pointer;
          };
        }
        .drop-showType-icon{
          transform: rotate(-90deg);
          position: absolute;
          right: 11px;
          top: 0px;
          font-size: 16px;
        }
      }
      button {
        // width: 70px;
        // height: 20px;
        border: none;
        outline: none;
        border-radius: 5px;
        background: rgb(28, 165, 252);
        color: #fff;
        // padding: 7px 25px;
        padding: 5px 20px;
        font-size: 12px;
        cursor: pointer;
        &:hover {
          background: #1995e4;
        }
      }
    }
  }
}
.picture-preview{
  width: 100%;
  height: auto;
  background: #fff;
  overflow: hidden;
  .remaining-img{
    padding-left: 10px;
    font-family: '';
    font-size: 14px;
    color: #757575
  }
  .preview-box{
    display: flex;
    justify-content: flex-start;
    flex-wrap: wrap;
    padding: 0 10px 10px 10px;
    min-height: 120px;
    background-position: center;
    background-repeat: no-repeat;
    background-size: auto;
    .small-img-box{
      width: 183px;
      height: 120px;
      overflow: hidden;
      position: relative;
      background-position: center;
      background-repeat: no-repeat;
      background-size: cover;
      margin: 10px 10px 0 0;
      .upload-pro{
        position: absolute;
        bottom: 0;
        width: 100%;
        height: 100%;
        color: #fff;
        background: rgba(0,0,0,0.7);
        display: flex;
        justify-content: center;
        align-items: center;
      }
      img{
        width: 100%;
        height: 100%;
        object-fit:cover
      }
      i{
        position: absolute;
        right: 0;
        color: red;
        top: 0;
      }
    }
  }
}
</style>





