<template>
  <div class="all-contact">
    <div
      class="contact-item"
      v-for="(item, index) in contactList"
      :key="index"
      @click="selectItem(item, index)"
      :title="item.buddyName"
    >
      <div class="contact-icon">
        <img :src="userIcon" alt="" @error="defaultIcon">
      </div>
      <div class="contact-name">{{item.buddyName}}</div>
      <div class="operating">
        <span v-if="selectStatus" class="select-status">
          <i v-if="item.isSelected" class="iconfont icon-selected"></i>
        </span>
        <i v-if="isRemoveBtn" class="iconfont icon-guanbi" @click="removeItem(index)"></i>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      userIcon: require('./../../assets/images/common/default.png')
    }
  },
  props: {
    contactList: Array | Object,
    selectStatus: Boolean,
    isRemoveBtn: Boolean
  },
  created() {
    // ...
  },
  methods: {
    selectItem(item, index) {
      this.$emit('select-item', item, index)
    },
    removeItem(index) {
      this.$emit('remove-item', index)
    },
    defaultIcon(e) {
      e.target.src = require('./../../assets/images/common/default.png');
    }
  }
}
</script>

<style lang="scss" scoped>
.all-contact{
  width: 100%;
  // height: 100%;
  .contact-item{
    display: flex;
    align-items: center;
    padding: 5px 10px;
    cursor: default;
    &:hover{
      background: #dedede;
    }
    .contact-icon{
      width: 40px;
      height: 40px;
      border-radius: 4px;
      overflow: hidden;
      img{
        width: 100%;
        height: 100%
      }
    }
    .contact-name{
      padding: 0 15px;
      width: 100%;
      overflow: hidden;
      flex: 1;
      text-overflow: ellipsis;
      white-space: nowrap;
    }
    .operating{
      width: 30px;
      .select-status{
        width: 20px;
        height: 20px;
        display: block;
        border-radius: 50%;
        border: 1px solid #b5b5b5;
        position: relative;
        .icon-selected{
          color: #1995e4;
          font-size: 20px;
          display: block;
          position: absolute;
          top: -2px;
          left: -1px;
        }
      }
      .icon-guanbi{
        cursor: pointer;
        color: #1995e4;
        border-radius: 50%;
      }
    }
  }
}
</style>



