import Vue from 'vue';
import i18n from './components/common/i18n.js';
if (!window.require) {
  window.require = window.nodeRequire;
}
i18n.locale = window.require('electron').remote.getGlobal('service').config.getLanguage();
export const format = (date) => {
  return getDateDiff(new Date(date));
};

export function getDateDiff (dateTimeStamp) {
  let minute = 1000 * 60;
  let hour = minute * 60;
  let day = hour * 24;
  let halfamonth = day * 15;
  let month = day * 30;
  let now = new Date().getTime();
  let diffValue = now - dateTimeStamp;
  if (diffValue < 0) {
    return;
  }
  let monthC = diffValue / month;
  let weekC = diffValue / (7 * day);
  let dayC = diffValue / day;
  let hourC = diffValue / hour;
  let minC = diffValue / minute;
  let result = '';
  if (monthC >= 1) {
    result = '' + parseInt(monthC) + i18n.t('monthAgo');
  } else if (weekC >= 1) {
    result = '' + parseInt(weekC) + i18n.t('weekAgo');
  } else if (dayC >= 1) {
    result = '' + parseInt(dayC) + i18n.t('dayAgo');
  } else if (hourC >= 1) {
    result = '' + parseInt(hourC) + i18n.t('hoursAgo');
  } else if (minC >= 1) {
    result = '' + parseInt(minC) + i18n.t('minAgo');
  } else {
    result = i18n.t('justNow');
  }
  return result;
}

export function parseTime (time, cFormat) {
  if (arguments.length === 0) {
    return null;
  }
  const format = '{m}-{d} {h}:{i}';
  // const format = '{y}-{m}-{d} {h}:{i}:{s}';
  let date;
  if (typeof time === 'object') {
    date = time;
  } else {
    if (('' + time).length === 10) time = parseInt(time) * 1000;
    date = new Date(time);
  }
  const formatObj = {
    y: date.getFullYear(),
    m: date.getMonth() + 1,
    d: date.getDate(),
    h: date.getHours(),
    i: date.getMinutes(),
    s: date.getSeconds(),
    a: date.getDay()
  };
  const timeStr = format.toString().replace(/{(y|m|d|h|i|s|a)+}/g, (result, key) => {
    let value = formatObj[key];
    if (key === 'a') return ['', '', '', '', '', '', ''][value - 1];
    if (result.length > 0 && value < 10) {
      value = '0' + value;
    }
    return value || 0;
  });
  return timeStr;
}
export function computeTime (addDayCount) {
  let date = new Date();
  date.setDate(date.getDate() + addDayCount);
  return date.getDate();
}
export function formatTime (time, option) {
  const d = new Date(time);
  const now = Date.now();
  const diff = (now - d) / 1000;
  if (diff < 30) {
    return i18n.t('justNow');
  } else if (diff < 3600) {
    return Math.ceil(diff / 60) + i18n.t('minAgo');
  } else if (diff < 3600 * 24) {
    return Math.ceil(diff / 3600) + i18n.t('hoursAgo');
    // } else if (diff < 3600 * 24 * 2) {
    //   // return '';
    //   if (computeTime(-1) === d.getUTCDate()) {
    //     return ' ' + (d.getHours() >= 10 ? d.getHours() : '0' + d.getHours()) + ':' + (d.getMinutes() >= 10 ? d.getMinutes() : '0' + d.getMinutes());
    //   }
  }
  if (option) {
    return parseTime(time, option);
  } else {
    return d.getMonth() + 1 + '' + d.getDate() + '' + d.getHours() + '' + d.getMinutes() + '';
  }
}
export const signalMonitor = new Vue();



