<template>
  <div id="app">
    <router-view></router-view>
  </div>
</template>

<script>
import Vue from 'vue';
export default {
  name: 'app',
  created() {
    console.log("get imsdkservice: ", this.$service);
    console.log("i get env: ", process.env.NODE_ENV);
    let dbPath = this.$service.config.userFilePath(null, resp => {
      dbPath = "";
      dbPath = resp.path + "cloudData/";
      Vue.prototype.$dbPath = dbPath
    }).path;
    dbPath += "cloudData/";
    //  
    if (process.env.NODE_ENV === "development") {
      let param = {
        user: "008615229023382",
        pwd: "123123",
        userType: 1,
        server: "vrv",
        nation: "0086"
      };
      this.$service.auth.login(param, resp => {
        console.log("i get login result: ", resp);
        if (resp.code === 0) {
          this.$router.push({ path: "/" });
        }
      });
    } else {
      Vue.prototype.$dbPath = dbPath
    }
  },
  mounted () {
    // vrv.init({
      // debug: true
    // });
  }
};
</script>
<style lang="scss">
#app {
  width: 100%;
  height: 100%;
  overflow: hidden;
  font-family: '';
  -webkit-font-smoothing: antialiased;
  -webkit-user-select: none;
}
.iconfont {
  font-family: "iconfont";
  font-style: normal;
  font-weight: normal;
  display: inline-block;
  font-size: 16px;
  &:hover {
    cursor: pointer;
  }
}
*{
  box-sizing: border-box;
}
