<template>
    <div v-if="commentBoxShow" class="comment-edit" ref="commentEdit">
        <div class="comment-text">
            <textarea
              v-model="messagContent"
              @focus="handleFocus"
              :placeholder="placeholder"
              :class="{'textarea-pla':textareaPla}"
              wrap="hard"
              :maxlength="1000"
            ></textarea>
        </div>
        <div class="operat">
            <div class="left">
                <!-- <i class="iconfont">&#xe70b;</i> -->
            </div>
            <div class="right">
                <span>{{$t('canEnterCharacters',{value:RemainingWord})}}</span>
                <button @click="posted" :disabled=!isPosted :style="{background:isPosted?'#1995e4':'#94d1f7'}">{{$t('publish')}}</button>
            </div>
        </div>
    </div>
</template>
<script>
// var bus = new Vue()
// import userInfo from '../../../assets/data/user'; // 
import Mixin from './../../common/mixin.js'
export default {
  mixins: [Mixin],
  data () {
    return {
      messagContent: '',
      hideMaskingBox: false,
      commentIndex: true,
      textareaPla: false,
      placeholder: this.$t('enterWords'),
      isPosted: false,
      RemainingWord: 1000
    };
  },
  props: {
    OpenCommentShowToEdit: Object,
    index: Number,
    account: Object,
    content: Object
  },
  computed: {
    commentBoxShow () {
      if (this.OpenCommentShowToEdit.index === this.index) {
        return true;
      } else {
        return false;
      }
    }
  },
  mounted () {
    document.addEventListener('click', e => {
      e.stopPropagation();
      if (this.commentBoxShow) {
        let hideEdit = document.getElementsByClassName('comment-edit')[0];
        if (!hideEdit.contains(e.target)) {
          this.OpenCommentShowToEdit.index = -1;
        };
      }
    });
  },
  updated() {
    if(this.commentBoxShow) {
      this.$refs.commentEdit.scrollIntoView(false);
    }
  },
  watch: {
    'commentBoxShow': function (val) {
      if (val === false) {
        this.textareaPla = false;
        this.placeholder = this.$t('enterWords');
      }
    },
    'messagContent': function (val) {
      if (this.messagContent === '') {
        this.isPosted = false;
      };
      for (let i = 0; i < this.messagContent.split('').length; i++) {
        if (this.messagContent.split('')[i] !== ' ') {
          this.isPosted = true;
          break;
        } else {
          this.isPosted = false;
        }
      };
      this.RemainingWord = 1000 - this.messagContent.split('').length;
    }
  },
  methods: {
    // placeholder
    handleFocus () {
      this.textareaPla = false;
      this.placeholder = this.$t('enterWords');
    },
    // 
    posted () {
      let commentInfo = {
        commentUser: {
          name: this.account.name,
          portraitURL: this.account.avatarUrl,
          sex: this.account.gender
        },
        commentID: ''
      };
      if (this.messagContent === '') {
        this.textareaPla = true;
        this.placeholder = `${this.$t('mustEnterContent')}!`;
        return;
      } else {
        let commentBean = {
          /*  */
          //account.mUserIdaccount.id
          commentUserID: this.account.id,
          /*  */
          content: this.messagContent,
          /*  */
          createTime: new Date().getTime(),
          /* ID */
          replyUserID: '',
          /* ID */
          subjectID: this.content.share.shareID
        };
        let postedParamsCB = {
          commentBean
          // 'success': (resp) => {
          //   console.log(resp, '');
          //   commentInfo.commentID = resp.commentID;
          //   if (resp.resultCode === 0) {
          //     this.$store.commit('SHARE_LIST', {flag: 6, comment: Object.assign(commentBean, commentInfo), index: this.index});
          //     this.messagContent = '';
          //     this.OpenCommentShowToEdit.index = -1;
          //   }
          // }
        };
        // vrv.jssdk.addComment(postedParamsCB);
        this.$agent.addComment(postedParamsCB,resp => {
          console.log(resp, '');
          commentInfo.commentID = resp.commentID;
          if (resp.resultCode === 0) {
            this.$store.commit('SHARE_LIST', {flag: 6, comment: Object.assign(commentBean, commentInfo), index: this.index});
            this.messagContent = '';
            this.OpenCommentShowToEdit.index = -1;
          }
        })
      };
    }
  }
};
</script>
<style lang="scss" scoped>
.comment-edit{
    *{
        box-sizing: border-box
    }
    width: 100%;
    height: auto;
    padding-top:15px;
    .comment-text{
        width: 100%;
        height: 70px;
        border: 1px solid rgb(128, 126, 126);
        overflow: hidden;
        textarea{
            width: 100%;
            height: 100%;
            border: 0;
            padding:5px;
            outline:none;
        }
        .textarea-pla{
            &::-webkit-input-placeholder{
                color:red;
            }
        }
    }
    .operat{
        display: flex;
        justify-content: space-between;
        height:auto;
        padding:12px 0;
        -webkit-user-select: none;
        .left{
            width: 100px;
            height:100%;;
            // background: red;
            .iconfont{
                font-size: 28px;
                color:rgb(117, 117, 117);
                &:hover{
                    color:rgb(255, 255, 0)
                }
            }
        }
        .right{
            width: auto;
            height: auto;
            font-size: 12px;
            span{
              color:rgb(185, 185, 185);
              padding-right:10px;
            }
            button{
                // width: 70px;
                // height: 20px;
                border: none;
                outline: none;
                border-radius: 5px;
                background: rgb(28, 165, 252);
                color:#fff;
                // padding:7px 25px;
                padding: 5px 20px;
                cursor: pointer;
                &:hover{
                    background: #1995e4
                }
            }
        }
    }
}
</style>





