// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue';
import App from './App';
import store from './store';
import router from './router';
import wui from './components/common/index.js';
import i18n from './components/common/i18n.js';
import iconfont from './assets/iconfont/iconfont.css';
import Agent from './agent';
Vue.use(wui);
Vue.use(iconfont);
Vue.config.productionTip = false;

//  SDK
let agent = navigator.userAgent.toLowerCase();
let dbPath = null;
let fs = null;

if (agent.indexOf('electron') !== -1) {
  if (!window.require) {
    window.require = window.nodeRequire;
  }
  dbPath = window.require('electron').remote.getGlobal('dbPath') + 'public/temp/';
  i18n.locale = window.require('electron').remote.getGlobal('service').config.getLanguage();
  console.log('i18n.locale:', i18n.locale);
  fs = window.require('fs');
} else {
  dbPath = 'C:/Users/vrv/AppData/Local/Linkdood/public/temp/';
  fs = '';
}

Vue.prototype.$dbPath = dbPath;
Vue.prototype.$fs = fs;
Vue.prototype.$agent = new Agent();

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  store,
  i18n,
  template: '<App/>',
  components: {
    App
  }
});



