<template>
  <div class="comment-favor" v-if="praiseList.length > 0">
    <i class="iconfont icon-dianzan"></i>
    <span v-for="(people,index) in praiseSumList" :key="index">
      <span class="favor-name">{{people.user.name}}</span>
      <span v-if="praiseList.length > 1 && index != praiseSumList.length - 1">,</span>
    </span>
    <span v-if="praiseList.length == 1">{{$t('likes')}}</span>
    <span v-else-if="praiseList.length > 20">{{$t('etclikes',{value:praiseList.length})}}</span>
    <span v-else>{{$t('totallikes',{value:praiseList.length})}}</span>
  </div>
</template>
<script>
export default {
  props: {
    praiseList: Array,
    index: Number
  },
  computed: {
    praiseSumList() {
      return this.praiseList.length > 20 ? this.praiseList.slice(0,20) : this.praiseList;
    }
  }
};
</script>
<style lang="scss" scoped>
.comment-favor {
  //
  background-color: rgb(218, 214, 218);
  padding: 5px;
  border-top-left-radius: 5px;
  border-top-right-radius: 5px;
  color: #3c91e9;
  // padding: 2px;
  //
  font-size: 12px;
  .iconfont {
    color: #ffffff;
    border-radius: 50%;
    font-size: 12px;
    padding: 3px;
    background: #3c91e9;
  }
  .favor-name{
    cursor: pointer;
    display: inline-block;
  }
}
</style>





