<template>
  <div class="modal" v-if="isShowModal">
    <div class="modal-background" @click="$emit('close')"></div>
    <div class="modal-card" :style="cardSize">
      <slot></slot>
    </div>
  </div>
</template>
<script>
export default {
  name: 'wModal',
  data() {
    return {

    }
  },
  props: {
    isShowModal: {
      type: Boolean,
      default: false
    },
    cardSize: String | Object
  }
}
</script>
<style lang="scss" scoped>
.modal{
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  right: 0;
  background: rgba(0, 0, 0, 0.6);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 999;
  .modal-background{
    position: absolute;
    top: 0;
    right: 0;
    z-index: -1;
    width: 100%;
    height: 100%;
  }
  .modal-card{
    width: 200px;
    height: 100px;
    overflow: hidden;
  }
}
</style>



