import { mapGetters } from 'vuex';
const mixin = {
  data () {
    return {}
  },
  computed: {
    ...mapGetters([])
  },
  methods: {
    lengthValidator (content, length) {
      if (content.length > length) {
        this.$toast.tip({message: '1000'});
        return true
      }
    }
  }
}
export default mixin;



