<template>
  <div class="content-title">
    <img class="title-avatar" @error="defaultImg" :src="userInfo.serverHost + share.shareUser.portraitURL">
    <div class="title-info">
      <h4 class="title-name">{{share.shareUser.name}}</h4>
      <span class="title-time">{{formatCreateTime(share.time)}}</span>
    </div>
  </div>
</template>
<script>
import * as DateUtils from '../../utils';
import { getDefaultIcon } from './../common/util.js';
export default {
  data () {
    return {
      defaultAvatar: require('../../assets/images/common/dou.png')
    };
  },
  props: {
    share: Object,
    userInfo: Object
  },
  methods: {
    formatCreateTime (time) {
      return DateUtils.getDateDiff(time);
    },
    defaultImg (e) {
      e.target.src = getDefaultIcon(this.share.shareUser.sex);
    }
  }
};
</script>
<style lang="scss" scoped>
.content-title {
  width: 100%;
  height: 100%;
  display: flex;
  flex-flow: row;
  .title-avatar {
    width: 50px;
    height: 50px;
    border-radius: 50%;
    margin: 0px 12px 0px 12px;
    background: #A4A8AC;
    border: 1px solid rgba(0, 0, 0, .05);
  }
  .title-info {
    display: flex;
    flex-flow: column;
    flex: 1;
    .title-name {
      margin: 5px 0px 0px;
      flex: 1;
      //
      font-size: 14px;
      color: #295c9d;
    }
    .title-time {
      // font-size: 14px;
      font-size: 12px;
      color: #A4A8AC;
      margin: 5px 0px 14px 0px;
      height: 100%;
      display: table-cell
    }
  }
}
</style>





