<template>
  <div class="change-cover">
    <div class="modal-head">
      <div class="head-title">{{$t('repCov')}}</div>
      <i class="iconfont icon-guanbi" @click="$emit('close')"></i>
    </div>
    <img-cropper
      class="img-cropper"
      @uploading="uploadLoading"
    ></img-cropper>
    <div class="modal-bottom">
      <button @click="$emit('close')">{{$t('cancel')}}</button>
      <button :style="{pointerEvents: isDisable?'none':'auto'}" class="confirm" @click="confirm">{{$t('confirm')}}</button>
    </div>
  </div>
</template>
<script>
import { mapGetters } from 'vuex';
import imgCropper from './imgCropper.vue';
export default {
  data() {
    return {
      base64Data: '',
      isDisable: false
    }
  },
  props: {
    isDisable: Boolean
  },
  computed: {
    ...mapGetters(['user_account'])
  },
  components: {
    imgCropper
  },
  methods: {
    uploadLoading(data) {
      this.base64Data = data;
    },
    confirm() {
      this.$emit('is-disable', true);
      let savePath = this.$dbPath + new Date().getTime() + '.jpg';
      console.log('savePath', savePath);
      let newBase64Data = this.base64Data.replace(/^data:image\/\w+;base64,/, "");
      let dataBuffer = new Buffer(newBase64Data, 'base64');
      this.$fs.writeFile(savePath, dataBuffer, (err) => {
        if(err){
          console.log(err)
          this.$toast.tip({message: this.$t('imageUploadFailed')});
        }else{
          console.log('');
          this.$emit('successful-screenshot', savePath)
        }
      });
    }
  }
};
</script>
<style lang="scss">
.change-cover{
  height: 100%;
  border-radius: 5px;
  overflow: hidden;
  background: #fff;
  .modal-head{
    display: flex;
    justify-content: space-between;
    padding: 5px;
    background: #2196F3;
    color: #fff;
  }
  .cropper{
    height: 370px!important;
  }
  .modal-bottom{
    padding: 10px;
    border-top: 1px solid #cfd0d4;
    text-align: end;
    button{
      border: none;
      outline: none;
      background: #cfd0d4;
      width: 75px;
      height: 30px;
      color: #fff;
      border-radius: 5px;
      cursor: pointer;
    }
    .confirm{
      background: #2196F3;
    }
  }
}
</style>



