<template>
  <div class="user-info">
    <div
      class="user-cover"
      :title="$t('replaceCov')"
      :style="{
        backgroundImage: `url(${this.user_account.backgroundUrl?this.user_account.serverHost + this.user_account.backgroundUrl:defaultCover})`
      }"
      @click.stop="openImgCroppers"
    >
    </div>
    <div class="img-avatar">
      <!-- mPortraitUrl avatarUrl-->
      <img
        :src="user_account.serverHost + user_account.avatarUrl"
        class="avatar"
        @error="defaultImg"
      >
      <div class="name" ref="name">{{user_account.name}}</div><!--mUserNamename -->
    </div>
    <w-modal :cardSize="cardSize" :isShowModal="isShowModal" @close="isShowModal = false">
      <img-croppers
        @close="isShowModal=false"
        @successful-screenshot="uploadCover"
        :isDisable="isDisable"
        @is-disable="isDisable = true"
      ></img-croppers>
    </w-modal>
  </div>
</template>
<script>
import { mapGetters } from 'vuex';
import { getDefaultIcon } from './../common/util.js';
import ImgCroppers from './../common/ImgCropper/index.vue';
export default {
  components: {
    ImgCroppers
  },
  data () {
    return {
      defaultCover: require('../../assets/images/common/defultCover.png'),
      cardSize: {
        width: '600px',
        height: 'auto'
      },
      isShowModal: false,
      isDisable: false,
    };
  },
  computed: {
    ...mapGetters(['user_account'])
  },
  methods: {
    // 
    uploadCover(path) {
      console.log(':::', path);
      let uploadImgUrlParam = {
        size: 1,
        urls: [{img: path}]
        // 'success': resp => {
        //   this.isDisable = false;
        //   if (resp.resultCode !== 0) {
        //     this.$toast.tip({message: this.$t('imageUploadFailed')});
        //     return
        //   };
        //   console.log(resp.mPhotoPath.url);
        //   this.delTempImg(path);
        //   this.saveUserCover(resp.mPhotoPath.url);
        // }
      };
      // vrv.jssdk.uploadShareImage(uploadImgUrlParam);
      this.$agent.uploadShareImage(uploadImgUrlParam, resp => {
        this.isDisable = false;
        if (resp.resultCode !== 0) {
          this.$toast.tip({message: this.$t('imageUploadFailed')});
          return
        };
        this.delTempImg(path);
        this.saveUserCover(resp.mPhotoPath.url);
      }, pro => {

      })
    },
    // 
    openImgCroppers() {
      console.log('');
      this.isShowModal = true;
    },
    // 
    saveUserCover(imgPath) {
      console.log('imgPath:::', imgPath, this.user_account.id);
      let setShareBgmParam = {
        shareBackgroundImg: {
          //user_account.mUserIduser_account.id
          userID: this.user_account.id,
          backgroundUrl: imgPath
        }
        // 'success': resp => {
        //   this.isDisable = false;
        //   this.isShowModal = false;
        //   if (resp.resultCode !== 0) {
        //     this.$toast.tip({message: this.$t('imageUploadFailed')});
        //     return
        //   }
        //   this.$store.commit('SET_ACCOUNT', {flag: 1, val: imgPath});
        //   this.$toast.tip({message: this.$t('successfulRep')});
        // }
      };
      // vrv.jssdk.uploadShareBackgroundIMG(setShareBgmParam);
      this.$agent.uploadShareBackgroundIMG(setShareBgmParam, resp => {
        this.isDisable = false;
        this.isShowModal = false;
        if (resp.resultCode !== 0) {
          this.$toast.tip({message: this.$t('imageUploadFailed')});
          return
        }
        this.$store.commit('SET_ACCOUNT', {flag: 1, val: imgPath});
        this.$toast.tip({message: this.$t('successfulRep')});
      })
    },

    // 
    delTempImg(path) {
      this.$fs.unlink(path, (err) => {
        if (err) throw err;
        console.log('');
      });
    },

    // 
    defaultImg (e) {
      let icon = this.user_account.gender ? this.user_account.gender : 0
      e.target.src = getDefaultIcon(icon);
    },
  }
};
</script>
<style lang="scss" scoped>
.user-info {
  height: auto;
  background: #cfd0d4;
  // position: fixed;
  // top: 0px;
  // z-index: 999;
  width: 100%;
  .user-cover{
    position: relative;
    width: 100%;
    height: 180px;
    background-size: cover;
  }
  .img-avatar {
    position: relative;
    width: 100%;
    height: 45px;
    padding-left: 10%;
    display: flex;
    .avatar {
      position: absolute;
      width: 80px;
      border-radius: 5px;
      height: 80px;
      top: -50px;
    }
    .name {
      align-self: flex-end;
      font-size: 14px;
      font-weight: 600;
      color: #295c9d;
      margin: 0 0 18px 90px;
    }
    .avatar-shadow{
      position: absolute;
      bottom: 0px;
      left: 0;
      height: 40px;
      width: 100%;
      background-color: #cfd0d4;
      z-index: -1;
    }
  }
}
</style>





