<template>
  <div class="set-power-modal">
    <div class="forward-list">
      <div class="left">
        <div class="top-search">{{$t('friendChoose')}}</div>
        <div class="left-list">
          <contact-list
            :contactList="contactList"
            :selectStatus="true"
            @select-item="selectItem"
            v-if="keyWords === ''"
          ></contact-list>
        </div>
      </div>
      <div class="right">
        <div class="right-top">
          <span>{{rightTip}}</span>
          <i class="iconfont icon-guanbi" @click="$emit('close')"></i>
        </div>
        <div class="selected-list">
          <contact-list
            :contactList="selectedContactList"
            :isRemoveBtn="true"
            @remove-item="removeItem"
          ></contact-list>
        </div>
        <div class="right-footer">
          <button
            @click="confirm"
            :disabled="isForward"
            :class="isForward?'forward-ban':'forward-ok'"
          >{{$t('confirm')}}</button>
          <button @click="$emit('close')">{{$t('cancel')}}</button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import ContactList from './contactList.vue';
const contactData = require('./../../../static/contactData.json');
const sousuoData = require('./../../../static/sousuo.json');
export default {
  components: {
    ContactList
  },
  data() {
    return {
      keyWords: '',
      contactList: {},
      selectedContactList: {},
      searchContant: {}
    }
  },
  props: {
    tempGroupMember: Array | Object
  },
  computed: {
    rightTip() {
      let selectNumber = Object.keys(this.selectedContactList).length
      return selectNumber > 0 ? this.$t('hasSelect', { value: selectNumber }) : this.$t('selectContact');
    },
    isForward() {
      return this.rightTip === this.$t('selectContact');
    },
  },
  created() {
    // 
    this.selectedContactList = this.tempGroupMember;
    this.getContactList();
    // console.log(contactData, '');
    // contactData.contacts.forEach((element, index) => {
    //   if (element.isApp === 2) {
    //     this.$set(element, 'isSelected', false);
    //     this.$set(this.contactList, element.id, element);
    //   }
    // });
    // console.log(this.contactList, 'seted');
  },
  methods: {
    setIsSelected(index, flag) {
      if (this.contactList.hasOwnProperty(index)) {
        this.$set(this.contactList[index], 'isSelected', flag);
      } else {
        this.$set(this.searchContant[index], 'isSelected', flag);
      };
    },
    selectItem(item, index) {
      if (item.isSelected && item.isSelected === true) {
        this.setIsSelected(index, false);
        this.$delete(this.selectedContactList, index);
      } else {
        this.setIsSelected(index, true);
        this.$set(this.selectedContactList, index, item);
      }
    },
    removeItem(index) {
      this.setIsSelected(index, false);
      this.$delete(this.selectedContactList, index);
    },
    confirm() {
      let setGroupType = {
        isTempGroup: false,
        groupMembers: this.selectedContactList
      }
      this.$emit('set-group-type', setGroupType);
      this.$emit('close');
      return;
      this.$alert.confirm({
        confirmText: this.$t('useNextTime'),
        title: this.$t('tip'),
        onConfirm: () => {
          let setGroupType = {
            isTempGroup: true,
            groupID: ''
          }
          this.$emit('set-group-type', setGroupType);
          this.$emit('close');
        },
        onCancel: () => {
          let setGroupType = {
            isTempGroup: false,
            groupMembers: this.selectedContactList
          }
          this.$emit('set-group-type', setGroupType);
          this.$emit('close');
        }
      })
    },
    getContactList() {
      let selectedUserId = Object.keys(this.selectedContactList);
      let getContactListParam = {
        // 'success': resp => {
        //   console.log(resp, '');
        //   resp.contacts.forEach((element, index) => {
        //     this.$set(element, 'isSelected', false);
        //     if (selectedUserId.indexOf(element.buddyID) >= 0)
        //       this.$set(element, 'isSelected', true);
        //     this.$set(this.contactList, element.buddyID, element);
        //   })
        // }
      }
      // vrv.jssdk.getContactList(getContactListParam)
      this.$agent.getContactList(getContactListParam, resp => {
        console.log(resp, '');
        resp.contacts.forEach((element, index) => {
          this.$set(element, 'isSelected', false);
          if (selectedUserId.indexOf(element.buddyID) >= 0)
            this.$set(element, 'isSelected', true);
          this.$set(this.contactList, element.buddyID, element);
        })
      })
    }
  }
}
</script>

<style lang="scss" scoped>
.set-power-modal{
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  background: #fff;
  border-radius: 4px;
  // padding: 10px;
  .forward-title{
    padding: 6px 10px;
    background: #565656;
    color: #fff;
  }
  .forward-list{
    width: 100%;
    height: 100%;
    display: flex;
    & > div{
      width: 50%;
      height: 100%;
      display: flex;
      flex-direction: column;
    }
    .left{
      display: flex;
      flex-direction: column;
      border-right: 1px solid #b5b5b5;
      .top-search{
        position: relative;
        padding: 10px 10px;
        .icon-sousuo{
          position: absolute;
          top: 17px;
          left: 14px;
        }
        input{
          width: 100%;
          height: 30px;
          outline: #fff;
          border: 1px solid #d0d0d0;
          border-radius: 4px;
          background: #d0d0d0;
          padding: 0 23px 0 24px;
          &:focus{
            background: #fff;
            &::-webkit-input-placeholder{
              color: #fff;
            }
            &+.icon-guanbi{
              display: block;
            }
          }
        }
      }
      .left-list{
        overflow-y: auto;
        height: 100%;
        .contact-ype-title{
          padding: 5px 10px;
          background: #c5c5c53d;
        }
      }
    }
    .right{
      display: flex;
      flex-direction: column;
      .right-top{
        display: flex;
        justify-content: space-between;
        padding: 10px;
        i{
          cursor: pointer;
        }
      }
      .selected-list{
        overflow-y: auto;
        height: 100%;
      }
      .right-footer{
        width: 100%;
        padding: 10px;
        display: flex;
        justify-content: flex-end;
        button{
          width: 70px;
          height: 25px;
          border-radius: 4px;
          outline: none;
          border: none;
          cursor: pointer;
          margin-left: 10px;
        }
        button:first-child{
          color: #fff;
        }
        button:last-child{
          color: #888888;
        }
        .forward-ban{
          background: rgba(82, 168, 236, 0.8);
          cursor: not-allowed;
        }
        .forward-ok{
          background: #1995e4;
        }
      }
    }
  }
}
</style>



