<template>
  <div class="comment">
    <div class="comment-info-box" :style="{padding: isCommentInfoBox? '3px' : '0'}">
      <CommentFavor
        :praiseList="content.praiseList"
        :index="index"
        :style="{borderBottom: commentLstData.length > 0? '1px solid #b9b9b9' : '0'}"
      ></CommentFavor>
      <CommentList
        :commentLst="commentLstData" 
        :account="userInfo" 
        :index="index"
      ></CommentList>
    </div>
    <CommentEdit
      :content="content" 
      :OpenCommentShowToEdit="OpenCommentShow" 
      :index="index" 
      :account="userInfo"
    ></CommentEdit>
  </div>
</template>
<script>
import CommentFavor from './CommentFavor.vue';
import CommentList from './CommentList.vue';
import CommentEdit from './CommentEdit.vue';
export default {
  data () {
    return {
      hideMaskingBox: ''
    };
  },
  components: {
    CommentFavor,
    CommentList,
    CommentEdit
  },
  props: {
    content: Object,
    OpenCommentShow: Object,
    index: Number,
    userInfo: Object
  },
  computed: {
    commentLstData () {
      return this.content.commentPage.commentLst;
    },
    isCommentInfoBox() {
      return this.content.praiseList.length > 0 || this.commentLstData.length > 0;
    }
  },
  created: function () {

  },
  // methods: {
  //   // 
  //   messageShow (message) {
  //     console.log(message, 'CommentEditemit');
  //     this.content.commentPage.commentLst.push(message);
  //     this.$emit('get-commentLst', true);
  //   }
  //   // 
  //   // closeMasking (param) {
  //   //   this.hideMaskingBox = param;
  //   //   this.$emit('close-masking', this.hideMaskingBox);
  //   // }
  // }
};
</script>
<style lang="scss">
.comment {
    margin: 0 40px 0 74px;
    font-size: 14px;
    -webkit-user-select: text;
  }
</style>





