<template>
  <div class="content-detail">
    <div class="detail-text"> <!-- v-html="isFullAll?content.share.content:content.share.content.substr(0,250)"-->
      <span class="text" v-html="innerText">
      </span>
      <span
        class="show-all-txt"
        v-if="isShowAllTxt"
        @click="isFullAll = !isFullAll"
      >{{isFullAll? $t('collapse'):$t('expandAll')}}</span>
    </div>
    <div class="detail-img" :style="{marginTop:marginTop+'px'}">
      <ul class="detail-img-ul clear-fix">
        <li
          v-for="(img,index) in content.share.detailContent.imgurls"
          class="img-li-default"
          :key="index"
        >
          <div class="img-div"
            @click="showBigImg(img,$event)"
          >
            <img
              :src="getFinallyUrl(img)"
              @error="defaultImg($event, index)"
            >
          </div>
        </li>
      </ul>
    </div>
    <div class="detail-address" v-if="content.share.address">
      <div>
        <i class="iconfont icon-weizhi-tianchong"></i>
        {{content.share.address}}
      </div>
    </div>
    <div class="detail-footer">
      <div class="right operat">
        <div class="footer-tag" v-if="content.share.userID == userInfo.id"> <!--userInfo.mUserIduserInfo.id-->
          <span>{{showTypeTxt}}</span>
        </div>
        <div class="footer-tag" v-if="content.share.userID.toString() == userInfo.id"> <!--userInfo.mUserIduserInfo.id-->
          <span class="deleteShare"
            :style="{cursor: 'pointer'}"
            @click="deleteShare(content.share.shareID.toString())"
          >{{$t('delete')}}</span>
        </div>
      </div>
      <div class="left operat">
        <div class="footer-tag">
          <i @click="favor(rePraiseId)" class="iconfont icon-dianzan" :style="{color:showMefavor?'orange':'', pointerEvents: isFavor?'auto':'none'}"></i>
        </div>
        <div class="footer-tag">
          <i @click="leaveMes" class="iconfont icon-ico_home_reply"></i>
        </div>
      </div>
    </div>

    <div class="imgMask" :style="{'margin-top':scrollTop+'px'}" v-if="isBigImgShow" @click.stop="hiddenBigImg">
      <div class="imgMask-close">
        <i class="iconfont icon-guanbi1"></i>
      </div>
      <div class="showImg" :style="{width:objWidth+'px',height:objHeight+'px'}">
        <img class="big-Img" :src="bigImg">
      </div>
    </div>
  </div>
</template>
<script>
export default {
  data () {
    return {
      favorNum: 10,
      marginTop: Number,
      overflow: 'hidden',
      realHeight: Number,
      isBigImgShow: false,
      bigImg: '',
      scrollTop: Number,
      objWidth: Number,
      objHeight: Number,
      openCommentEdit: true,
      isFavor: true,
      isFullAll: false
    };
  },
  props: {
    content: Object,
    index: Number,
    userInfo: Object,
    myFavor: Number
  },
  computed: {
    showTypeTxt () {
      switch (this.content.share.scope) {
        case 'public': {
          return this.$t('public');
        }
        case 'private': {
          return this.$t('private');
        }
        case '1': {
          return this.$t('partFriends');
        }
        case '2': {
          return this.$t('privateTo');
        }
      }
    },
    showMefavor () {
      let a = true;
      for (let i = 0; i < this.content.praiseList.length; i++) {
        //userInfo.mUserIduserInfo.id
        if (this.userInfo.id === this.content.praiseList[i].userID.toString()) {
          a = false;
          return true;
        }
      };
      if (a) return false;
    },
    rePraiseId () {
      let pID = '';
      for (let i = 0; i < this.content.praiseList.length; i++) {
        //userInfo.mUserIduserInfo.id
        if (this.userInfo.id === this.content.praiseList[i].userID.toString()) {
          pID = this.content.praiseList[i].praiseID;
          break;
        }
      }
      return pID;
    },
    innerText () {
      let shareTxt = this.content.share.content ? this.content.share.content:' ';
      return (this.isFullAll? shareTxt : shareTxt.substr(0,250)).replace(/\n/g, '<br/>')
    },
    isShowAllTxt () {
      return this.content.share.content && this.content.share.content.length > 250
    }
  },
  methods: {
    // 
    defaultImg (e, i) {
      e.target.src = require('./../../assets/images/common/error.png');
    },
    // 
    deleteShare (shareID) {
      console.log(this.index, shareID);
      this.$alert.confirm({
        confirmText: `${this.$t('confirmDelete')}?`,
        title: `${this.$t('notification')}!`,
        onConfirm: () => {
          let deleteShareParam = {
            shareID: shareID
            // 'success': resp => {
            //   if (resp.resultCode === 0) {
            //     this.$store.commit('SHARE_LIST', {flag: 3, index: this.index});
            //     this.$toast.tip({message: this.$t('deleteSuccessfully')});
            //   }
            // }
          };
          // vrv.jssdk.deleteShare(deleteShareParam);
          this.$agent.deleteShare(deleteShareParam,resp => {
            if (resp.resultCode === 0) {
              this.$store.commit('SHARE_LIST', {flag: 3, index: this.index});
              this.$toast.tip({message: this.$t('deleteSuccessfully')});
            }
          })
        }
      })
    },
    /**
     * 
     */
    favor (rpId) {
      if (this.showMefavor === false) {
        this.isFavor ? this.praise() : '';
      } else {
        this.isFavor ? this.delPraise(rpId) : '';
      }
    },
    /**
     * 
     */
    praise () {
      console.log('');
      this.isFavor = false;
      let praiseShowInfo = {
        createTime: new Date().getTime(),
        praiseID: '',
        replyUserID: this.content.share.userID,
        subjectID: this.content.share.shareID,
        subjectType: 0,
        type: 1,
        //userInfo.mUserIduserInfo.id
        userID: this.userInfo.id,
        user: {
          name: this.userInfo.name,
          oriPortraitURL: this.userInfo.avatarUrl,
          portraitURL: this.userInfo.avatarUrl,
          sex: 1,
          //userInfo.mUserIduserInfo.id
          userID: this.userInfo.id
        }
      };
      let praiseBean = {
        createTime: new Date().getTime(),
        replyUserID: this.content.share.userID,
        subjectID: this.content.share.shareID,
        type: 1,
        //userInfo.mUserIduserInfo.id
        userId: this.userInfo.id
      };
      /**
       * 
       */
      let favorParamsCB = {
        praiseBean
        // 'success': (resp) => {
        //   console.log(resp, '');
        //   praiseShowInfo.praiseID = resp.praiseID || resp.resultMessage;
        //   if (resp.resultCode === 0) {
        //     console.log(praiseShowInfo, '');
        //     this.$store.commit('SHARE_LIST', {flag: 4, index: this.index, praiseShowInfo: praiseShowInfo});
        //     this.isFavor = true;
        //   }
        // }
      };
      // vrv.jssdk.addPraise(favorParamsCB);
      this.$agent.addPraise(favorParamsCB,resp => {
        console.log(resp, '');
        praiseShowInfo.praiseID = resp.praiseID || resp.resultMessage;
        if (resp.resultCode === 0) {
          console.log(praiseShowInfo, '');
          this.$store.commit('SHARE_LIST', {flag: 4, index: this.index, praiseShowInfo: praiseShowInfo});
          this.isFavor = true;
        }
      })
    },
    /**
     * 
     */
    delPraise (rpId) {
      console.log('', rpId);
      this.isFavor = false;
      let praiseBean = {
        subjectID: this.content.share.shareID,
        praiseID: rpId,
        type: 2
      };
      let deletePraiseCB = {
        praiseBean
        // 'success': (resp) => {
        //   if (resp.resultCode === 0) {
        //     this.$store.commit('SHARE_LIST', {flag: 5, index: this.index});
        //     this.isFavor = true;
        //   }
        // }
      };
      // vrv.jssdk.deletePraise(deletePraiseCB);
      this.$agent.deletePraise(deletePraiseCB,resp => {
        if (resp.resultCode === 0) {
          this.$store.commit('SHARE_LIST', {flag: 5, index: this.index});
          this.isFavor = true;
        }
      })
    },
    // formatImgThumb (img) {
    //   console.log(img);
    //   // let format = img.replace('thumbnail', 'thumb180');
    //   // format = format.replace('wx1', 'wx3');
    //   return img;
    // },
    getFinallyUrl(img) {
      if (img.indexOf('file::::') === 0) {
        return img.replace('file::::', '')
      }
      let completePath = img.indexOf('http') !== 0 ? this.userInfo.serverHost + img:img;
      return completePath;
    },
    // 
    showBigImg (img, event) {
      let tempImg = '';
      if (img.indexOf('file::::') === 0) {
        tempImg = img.replace('file::::', '');
      } else {
        tempImg = img.indexOf('http') !== 0 ? this.userInfo.serverHost + img : img;
      }
      [this.bigImg, this.isBigImgShow, this.scrollTop] = [tempImg, true, event.pageY - event.clientY];
      this.AutoResizeImage(796, 526, tempImg);
      document.querySelector('html').style.overflow = 'hidden';
    },
    // 
    hiddenBigImg () {
      this.isBigImgShow = !this.isBigImgShow;
      document.querySelector('html').style.overflow = '';
    },
    // 
    AutoResizeImage (maxWidth, maxHeight, objImg) {
      let img = new Image();
      img.src = objImg;
      let [hRatio, wRatio, Ratio, w, h] = [Number, Number, 1, img.width, img.height];
      [hRatio, wRatio] = [maxHeight / h, maxWidth / w];
      if (maxWidth === 0 && maxHeight === 0) {
        Ratio = 1;
      } else if (maxWidth === 0) {
        if (hRatio < 1) Ratio = hRatio;
      } else if (maxHeight === 0) {
        if (wRatio < 1) Ratio = wRatio;
      } else if (wRatio < 1 || hRatio < 1) {
        Ratio = (wRatio <= hRatio ? wRatio : hRatio);
      }
      if (Ratio < 1) {
        w = w * Ratio;
        h = h * Ratio;
      }
      [this.objHeight, this.objWidth] = [h, w];
    },
    // 
    leaveMes () {
      let openCommentEditPar = {
        openCommentEdit: this.openCommentEdit,
        index: this.index
      };
      this.$emit('leave-message', openCommentEditPar);
    }
  }
};
</script>
<style lang="scss" scoped>
.content-detail {
  padding: 0px 20px 10px 74px;
  height:auto;
  .detail-text {
    padding-bottom: 8px;
    // height:auto;
    -webkit-user-select: text;
    .text-read {
      font-size: 14px;
      cursor: pointer;
      color: #FFA286;
    }
    .text {
      font-size: 14px;
      height:auto;
      overflow:hidden;
      word-wrap:break-word;
    }
    .show-all-txt{
      color: rgba(255, 102, 0, 0.952);
      font-size: 14px;
      cursor: pointer;
      display: inline-block
    }
  }
  .detail-img {
    width: 342px;
    .detail-img-ul {
      margin: 0;
      padding: 0;
      list-style: none;
      .img-li-default {
        float: left;
        width: 110px;
        height: 110px;
        margin-top: 4px;
        margin-right: 4px;
      }
      .img-div {
        width: 110px;
        height: 110px;
        overflow: hidden;
        background: #e2e2e2;
        img{
          width: 100%;
          height: 100%;
          object-fit:cover;
          display: inline-block;
        }
      }
    }
    .clear-fix::after {
      content: '';
      display: block;
      clear: both;
    }
  }
  .detail-address {
    color: #FFA286;
    font-size: 14px;
    margin-top: 5px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    div:last-child{
      cursor: pointer;
    }
  }
  .detail-footer {
    width: 100%;
    margin-top: 20px;
    color: #909090;
    display: flex;
    justify-content: space-between;
    .operat{
      display: flex;
      .footer-tag {
        margin-right: 20px;
        color: inherit;
        // font-size: 14px;
        //
        font-size: 12px;
        cursor: pointer;
        .deleteShare{
          color: #295c9d;
        }
        .show-me-favored{
          color:orange
        }
        .iconfont {
          // font-size: 14px;
          color: #949393;
        }
      }
    }
    .left{
      justify-content: flex-start;
    }
    .right{
      justify-content: flex-end
    }
  }
  .imgMask {
    position: absolute;
    height: 100%;
    width: 100%;
    top: 0;
    left: 0;
    z-index: 100;
    background: rgba(0, 0, 0, .7);
    .imgMask-close {
      color: #ffffff;
      right: 0;
      position: absolute;
      z-index: 999;
      .iconfont {
        font-size: 20px;
        &:hover{
          color: #FF5252;
          cursor: pointer;
        }
      }
    }
  }
  .showImg {
    height: 100%;
    width: auto;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    // border: 10px solid #fff;
  }
  .big-Img {
    width: 100%;
    height: 100%;
  }
}
</style>





