import config from './config.js';

export function editBoxGetFocus(el) {
  el.focus();
  var range = document.createRange();
  range.selectNodeContents(el);
  range.collapse(false);
  var sel = window.getSelection();
  sel.removeAllRanges();
  sel.addRange(range);
}

export function getDefaultIcon(gender) {
  return config.defalutAvatar[gender];
}

export function versionCompire(ver1, ver2) {
  let version1pre = parseFloat(ver1);
  let version2pre = parseFloat(ver2);
  let version1next = ver1.replace(version1pre + ".", "");
  let version2next = ver2.replace(version2pre + ".", "");
  if (version1pre > version2pre) {
    return true;
  } else if (version1pre < version2pre) {
    return false;
  } else {
    if (version1next >= version2next) {
      return true;
    } else {
      return false;
    }
  }
}


