<template>
  <div class="outer-container">
    <!--  -->
    <UserInfo v-if="user_account"></UserInfo>
    <div class="container">
      <!--  -->
      <ShareEditArea></ShareEditArea>
      <div
        class="content-list"
        v-for="(content, index) in share_list"
        :key="index"
        v-if="content.share.shareUser"
        @click="logThisInfo(content)"
      >
        <!--  -->
        <ContentTitle
          :share="content.share"
          :userInfo="user_account"
        >
        </ContentTitle>
        <!--  -->
        <ContentDetail
          v-if="content"
          :user-info="user_account"
          :content="content"
          :index="index"
          @leave-message="getOpenCommentEvent"
        >
        </ContentDetail>
        <!--  -->
        <Comment
          class="comment"
          :userInfo="user_account"
          :index="index"
          :content="content"
          :OpenCommentShow="commentShowA"
        >
        </Comment>
      </div>
      <!--  -->
      <div class="more">
        <img v-if="isLoading" class="loading-gif"  src="./../../assets/images/common/loading.gif" alt="">
        <span @click="getMoreShare">{{tipTxt}}</span>
      </div>
    </div>
  </div>
</template>
<script>
import { mapGetters } from 'vuex';
import ContentTitle from './ContentTitle.vue';
import ContentDetail from './ContentDetail.vue';
import Comment from './Comment/index.vue';
import UserInfo from '../UserInfo/index.vue';
// import datamock from '../../assets/data/u2testData.js';
// import userInfomock from '../../assets/data/userInfo.js';
import ShareEditArea from './ShareEditArea.vue';
export default {
  data () {
    return {
      commentShowA: {},
      pageNum: 2,
      tipTxt: `${this.$t('loading')}...`,
      isLoading: true
    };
  },
  components: {
    ContentTitle,
    ContentDetail,
    Comment,
    UserInfo,
    ShareEditArea
  },
  created () {
    // vrv.ready(() => {
      this.getAccountInfo();
    // });
    // this.$store.commit('SHARE_LIST', {flag: 0, list: datamock.content});
    // this.$store.commit('SET_ACCOUNT', {flag: 0, val: userInfomock});
  },
  watch: {
    share_list: {
      handler: function(val) {
        if (this.tipTxt === this.$t('shareIsEmpty') && val.length > 0) {
          this.tipTxt = this.$t('noMore');
        }
      },
      deep: true
    }
  },
  computed: {
    ...mapGetters(['user_account', 'share_list']),
    praiseList() {
      return [];
    }
  },
  methods: {
    // 
    getMoreShare () {
      if (this.tipTxt !== this.$t('loadMore')) return;
      this.isLoading = true;
      let shareParam = {
        // userID: this.user_account.mUserId,
        userID: this.user_account.id,
        pageSize: 15,
        showType: 2,
        pageNum: this.pageNum
      };
      let commentParam = {
        pageSize: 1000,
        pageNum: 1,
        subjectType: 0
      };
      let getShareByPageParam = {
        shareParam,
        commentParam
        // 'success': resp => {
        //   if (resp.content === undefined || resp.content.length === 0) {
        //     this.isLoading = false;
        //     this.tipTxt = this.$t('noMore');
        //   } else {
        //     this.isLoading = false;
        //     let tempData = this.getMyFavored(resp.content)
        //     this.$store.commit('SHARE_LIST', {flag: 1, list: tempData});
        //     this.pageNum++;
        //   }
        // }
      };
      // vrv.jssdk.getShareList(getShareByPageParam);
      this.$agent.getShareList(getShareByPageParam,resp => {
        if (resp.content === undefined || resp.content.length === 0) {
            this.isLoading = false;
            this.tipTxt = this.$t('noMore');
          } else {
            this.isLoading = false;
            let tempData = this.getMyFavored(resp.content)
            this.$store.commit('SHARE_LIST', {flag: 1, list: tempData});
            this.pageNum++;
          }
      })
    },
    getAccountInfo () {
      let userParamsCB = {
        // 'success': resp => {
        //   resp.serverHost = resp.serverHost + '/';
        //   console.log(JSON.stringify(resp), '');
        //   this.$set(resp, 'backgroundUrl', '');
        //   this.$store.commit('SET_ACCOUNT', {flag: 0, val: resp});
        //   // 
        //   this.getUserCover();
        //   // 
        //   this.getShareLists()
        // }
      };
      // vrv.jssdk.getAccountInfo(userParamsCB);
      this.$service.user.getAccountInfo(userParamsCB, resp => {
        console.log("-------------",resp);
        let res = this.$service.config.getLoginExData({
          server: resp.server
        });
        console.log('getLoginExData:res:',res)
        if (JSON.parse(res.Orijson).UpURL) {
          resp.serverHost = JSON.parse(res.Orijson).UpURL;
        } else {
          resp.serverHost = JSON.parse(res.Orijson).serverhost.apHost
        };
        resp.serverHost = resp.serverHost + '/';
        console.log(JSON.stringify(resp), '');
        this.$set(resp, 'backgroundUrl', '');
        this.$store.commit('SET_ACCOUNT', {flag: 0, val: resp});
        // 
        this.getUserCover();
        // 
        this.getShareLists();
      })
    },

    // 
    getUserCover() {
      console.log('this.user_account.id:::', this.user_account.id);
      let getShareBgmParam = {
        // userID: this.user_account.mUserId
        userID: this.user_account.id
        // 'success': resp => {
        //   if (resp.setBackgroundUrl && resp.backgroundUrl) {
        //     this.$store.commit('SET_ACCOUNT', {flag: 1, val: resp.backgroundUrl});
        //   }
        // }
      };
      // vrv.jssdk.getShareBackgroundIMG(getShareBgmParam);
      this.$agent.getShareBackgroundIMG(getShareBgmParam, resp => {
        // if (resp.setBackgroundUrl && resp.backgroundUrl) {
        if(resp.backgroundUrl) {
          this.$store.commit('SET_ACCOUNT', {flag: 1, val: resp.backgroundUrl});
        }
      })
    },

    // 
    getShareLists() {
      let [shareParam, commentParam] = [{
        //userid
        userID: this.user_account.id,
        pageSize: 15,
        showType: 2,
        pageNum: 1
      },
      {
        pageSize: 1000,
        pageNum: 1,
        subjectType: 0
      }];
      let listParamsCB = {
        shareParam,
        commentParam
        // 'success': resps => {
        //   if (resps.content === undefined || resps.content.length === 0) {
        //     this.isLoading = false;
        //     this.tipTxt = this.$t('shareIsEmpty');
        //   } else {
        //     let tempData = this.getMyFavored(resps.content)
        //     this.$store.commit('SHARE_LIST', {flag: 0, list: tempData});
        //     this.isLoading = false;
        //     this.tipTxt = this.$t('loadMore');
        //   }
        // }
      };
      // vrv.jssdk.getShareList(listParamsCB);
      this.$agent.getShareList(listParamsCB,resp => {
          if (resp.content === undefined || resp.content.length === 0) {
            this.isLoading = false;
            this.tipTxt = this.$t('shareIsEmpty');
          } else {
            let tempData = this.getMyFavored(resp.content)
            this.$store.commit('SHARE_LIST', {flag: 0, list: tempData});
            this.isLoading = false;
            this.tipTxt = this.$t('loadMore');
          }
        }
      )
      
    },
    
    // 
    logThisInfo(content) {
      console.log(content, '')
    },

    // 
    getOpenCommentEvent (param) {
      console.log(param, 'ContentDetailemit');
      this.commentShowA = param;
    },

    getMyFavored (res) {
      for (let i = 0; i < res.length; i++) {
        if (res[i].commentPage.commentLst === undefined) {
          let AppendCommentLst = {
            commentLst: []
          };
          Object.assign(res[i].commentPage, AppendCommentLst);
        };
        if (res[i].praiseList === undefined) {
          let AppendPraiseList = {
            praiseList: []
          };
          Object.assign(res[i], AppendPraiseList);
          continue;
        };
      };
      return res;
    }
  }
};
</script>
<style lang="scss" scoped>
@keyframes spin {
  from {transform: rotate(0deg);}
  to {transform: rotate(360deg);}
}
.outer-container {
  // position: relative;
  overflow: hidden;
  overflow-y: auto;
  height:100%;
  width: 100%;
  .container {
    // overflow-x: hidden;
    // overflow-y: scroll;
    width: 600px;
    height: 100%;
    margin: 0 auto;
    padding-top: 10px;
    .content-list {
      padding: 10px 0 10px 0px;
      background: #ffffff;
      // position: relative;
      margin-bottom: 1px;
      .comment{
        // position: relative;
        // z-index:999;
      }
      .masking{
        // background: red;
        height: 100%;
        width: 100%;
        position: absolute
      }
    }
  }
   ::-webkit-scrollbar {
    display: none;
  }
  .no-container{
    color: rgb(177, 177, 177);
    text-align: center;
    padding-top: 30px;
    padding: 10px;
    background: #fff;
  }
}
.more{
  width: 250px;
  height: auto;
  margin: 0 auto;
  position: relative;
  color: #807e7e;
  padding-left: 70px;
  margin-top: 10px;
  margin-bottom: 20px;
  position: relative;
  //7/3
  font-size: 14px;
  span{
    cursor: pointer;
  }
  .loading-gif{
    width: 22px;
    position: absolute;
    top: -1px;
    left: 40px;
  }
}
</style>





